import { FormEvent, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Button, Input, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components';

import { login } from '../../services/auth/auth.reducer';
import { isLoginFailed, loginErrorMessage } from '../../services/auth/auth.selector';
import { useAppDispatch, useAppSelector } from '../../hooks/use-store';

import styles from './login-page.module.css';

function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const isValidForm = email && password.length >= 6;

  const dispatch = useAppDispatch();

  const [loginError, setLoginError] = useState('');
  const isFailed = useAppSelector(isLoginFailed);
  const errMsg = useAppSelector(loginErrorMessage);

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    dispatch(login({ email, password }));
  };

  useEffect(() => {
    if (isFailed) {
      setLoginError(
        (errMsg as string).includes('email or password are incorrect')
          ? '   '
          : ' '
      );
    }
  }, [errMsg, isFailed]);

  return (
    <>
      <form className={`${styles.main} pt-20`} onSubmit={handleSubmit}>
        <h2 className="text text_type_main-medium pb-6"></h2>

        {/* Email */}
        <div className="pb-6">
          <Input
            type="email"
            placeholder="E-mail"
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            name="email"
            error={isFailed}
            errorText={loginError}
            size="default"
          />
        </div>

        {/* Password */}
        <div className="pb-6">
          <PasswordInput
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            name="password"
          />
        </div>

        <Button type="primary" size="medium" htmlType="submit" disabled={!isValidForm}>
          
        </Button>
      </form>

      <div className={`${styles.main} pt-20`}>
        <p className="text text_type_main-default pb-4">
             ?
          <Link to="/register" className={styles.link}>
            {' '}
            
          </Link>
        </p>
        <p className="text text_type_main-default">
           ?
          <Link to="/forgot-password" className={styles.link}>
            {' '}
             
          </Link>
        </p>
      </div>
    </>
  );
}

export default LoginPage;
